import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

const Wrapper = styled.main`
  position: relative;
  min-height: calc(100vh - 127.67px);
  background-color: #fff;
  z-index: 1;
  .info {
    .chart {
      .lh-21 {
        height: 21px;
        line-height: 21px;
        font-family: 'Mochiy Pop P One', sans-serif;
      }
      .lh-24 {
        height: 24px;
        line-height: 24px;
      }
      .lh-42 {
        height: 42px;
        line-height: 42px;
      }
      .lh-90 {
        height: 90px;
        line-height: 90px;
        font-family: 'Mochiy Pop P One', sans-serif;
      }
      .site {
        color: cadetblue;
      }
      .chart__inner {
        position: relative;
        background: #fff;
        svg {
          min-width: 140px;
          min-height: 140px;
        }
        .text-wrapper {
          position: absolute;
          top: 0;
          width: 100%;
          min-width: 239px;
          height: 100%;
          min-height: 144px;
          display: flex;
          justify-content: center;
          align-items: center;
          color: #2b588d;
        }
      }
      .grade {
        margin-left: 1.1rem;
        .range {
          height: 280px;
          font-size: 12px;
          top: -10px;
          left: -24px;
          .number {
            height: 40px;
            text-align: right;
          }
        }
        .color {
          width: 10px;
          height: 40px;
        }
        .color-1 {
          background-color: rgb(151, 255, 165);
        }
        .color-2 {
          background-color: #fffd98;
        }
        .color-3 {
          background-color: #ff9f1c;
        }
        .color-4 {
          background-color: #f8333c;
        }
        .color-5 {
          background-color: #9b489b;
        }
        .color-6 {
          background-color: rgb(122, 17, 23);
        }
        .text {
          width: 70px;
          font-size: 12px;
        }
      }
    }
    .data {
      table {
        width: 100%;
        height: auto;
        background: #fff;
        color: #121212;
        .b-bottom {
          border-bottom: 1px solid #eee !important;
        }
        tr {
          border-bottom: 1px solid #eee;
        }
      }
    }
  }
  .video {
    width: 100%;
    min-height: 373px;
    .video_bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .video_mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #e1f1ffe2;
    }
    .video__container {
      position: relative;
      width: 100%;
      height: 100%;
      z-index: 3;
      .video__title {
        background-color: #2c92ec78;
        color: white;
        font-size: 24px;
      }
      .video__inner {
        position: relative;
        width: 100%;
        height: 0;
        padding-bottom: 56.25%;
        background-color: #e0e0e0;
        iframe {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .f-14 {
    font-size: 14px !important;
  }
  .f-28 {
    font-size: 28px !important;
    font-weight: 700 !important;
  }
  .f-60 {
    font-size: 60px !important;
    font-weight: 700 !important;
  }
  @media (min-width: 768px) and (max-width: 799.98px) {
    .chart__inner .text {
      transform: scale(0.8);
    }
  }
  @media (max-width: 1199.98px) {
    .grade {
      margin-left: 1.5rem !important;
    }
  }
  @media (max-width: 991.98px) {
    .grade {
      margin-left: 1.1rem !important;
      transform: scale(0.9);
      top: -10px !important;
      left: 0.5rem !important;
    }
  }
  @media (max-width: 424.98px) {
    .chart__inner .text {
      transform: scale(0.9);
    }
    .grade {
      transform: scale(0.8);
      top: -23px !important;
      left: 0.2rem !important;
    }
    .f-28 {
      font-size: 24px !important;
    }
    .video__container .video__title {
      font-size: 20px !important;
    }
  }
  @media (max-width: 374.98px) {
    .chart__inner .text {
      transform: scale(0.8);
    }
    .f-28 {
      font-size: 20px !important;
    }
  }
  @media (max-width: 319.98px) {
    .chart__inner .text {
      transform: scale(0.7);
    }
    .grade {
      transform: scale(0.7);
      top: -36px !important;
      left: -0.4rem !important;
    }
  }
`;

function Main({
  PublishTime,
  County,
  SiteName,
  AQI,
  PM2_AVG,
  PM2,
  PM10_AVG,
  PM10,
  O3_8hr,
  O3,
  CO_8hr,
  CO,
  SO2,
  NO2,
  children,
}) {
  const [svgWidth, setSvgWidth] = useState(null);

  useEffect(() => {
    if (!svgWidth) {
      let leftWidth = document.getElementById('chart').clientWidth;
      let width = leftWidth * 0.5;
      setSvgWidth(width);
    }
  }, [svgWidth]);
  useEffect(() => {
    window.onresize = () => {
      let leftWidth = document.getElementById('chart').clientWidth;
      let width = leftWidth * 0.5;
      if (svgWidth !== width) {
        setSvgWidth(width);
      }
    };
  }, [svgWidth]);

  return (
    <Wrapper>
      <div className="container-xl pb-3">
        <div className="row justify-content-center info">
          <div className="col-12 col-md-6 chart" id="chart">
            <div className="d-flex flex-column justify-content-center align-items-center p-3 bg-white site">
              <span className="lh-24">
                {PublishTime ? PublishTime : '2022/02/22 08:00:00'}
              </span>
              <span className="f-28 lh-42">
                {County ? County + '/' : '臺北市/'}
                {SiteName ? SiteName : '士林'}
              </span>
            </div>
            <div className="d-flex justify-content-center align-items-center mb-4 chart__inner">
              <svg
                width={svgWidth + svgWidth / 10}
                height={svgWidth + svgWidth / 10}
              >
                <circle
                  cx={svgWidth / 2 + svgWidth / 20}
                  cy={svgWidth / 2 + svgWidth / 20}
                  r={svgWidth / 2}
                  stroke={
                    AQI <= 50
                      ? 'rgb(151, 255, 165)'
                      : AQI <= 100
                      ? '#FFFD98'
                      : AQI <= 150
                      ? '#ffc459'
                      : AQI <= 200
                      ? '#ff5b63'
                      : AQI <= 300
                      ? '#9B489B'
                      : AQI <= 500
                      ? 'rgb(122, 17, 23)'
                      : 'rgb(151, 255, 165)'
                  }
                  strokeWidth={svgWidth / 10}
                  strokeLinecap={'round'}
                  fill={'#fff'}
                />
              </svg>
              <div className="text-wrapper">
                <div className="text">
                  <div className="text-center f-14 lh-21">空氣品質指標AQI</div>
                  <div className="text-center f-60 lh-90">{AQI || 0}</div>
                  <div className="text-center f-14 lh-21">
                    {AQI <= 50
                      ? '良好'
                      : AQI <= 100
                      ? '普通'
                      : AQI <= 150
                      ? '對敏感族群不健康'
                      : AQI <= 200
                      ? '對所有族群不健康'
                      : AQI <= 300
                      ? '非常不健康'
                      : AQI <= 500
                      ? '危害'
                      : '良好'}
                  </div>
                </div>
              </div>
            </div>
            <div className="position-absolute top-0 left-0 mt-3 grade">
              <div className="position-absolute range">
                <div className="number">500</div>
                <div className="number">300</div>
                <div className="number">200</div>
                <div className="number">150</div>
                <div className="number">100</div>
                <div className="number">50</div>
                <div className="number">0</div>
              </div>
              <div className="d-flex justify-content-start align-items-center">
                <div className="color color-6"></div>
                <div className="ps-1 text">危害</div>
              </div>
              <div className="d-flex justify-content-start align-items-center">
                <div className="color color-5"></div>
                <div className="ps-1 text">非常不健康</div>
              </div>
              <div className="d-flex justify-content-start align-items-center">
                <div className="color color-4"></div>
                <div className="ps-1 text">對所有族群不健康</div>
              </div>
              <div className="d-flex justify-content-start align-items-center">
                <div className="color color-3"></div>
                <div className="ps-1 text">對敏感族群不健康</div>
              </div>
              <div className="d-flex justify-content-start align-items-center">
                <div className="color color-2"></div>
                <div className="ps-1 text">普通</div>
              </div>
              <div className="d-flex justify-content-start align-items-center">
                <div className="color color-1"></div>
                <div className="ps-1 text">良好</div>
              </div>
            </div>
          </div>
          <div className="col-12 col-md-6 data">
            <table className="w-100">
              <thead>
                <tr className="row mx-0">
                  <th className="col-5 py-3">
                    <span className="px-2">污染物質</span>
                  </th>
                  <th className="col-7 text-center py-3">
                    <span className="px-2">濃度</span>
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr className="row mx-0">
                  <td className="col-5">
                    <div className="d-flex flex-column p-2">
                      <span>
                        PM<sub>2.5</sub>
                      </span>
                      <span className="f-14">
                        (μg/m<sup>3</sup>)
                      </span>
                      <span>細懸浮微粒</span>
                    </div>
                  </td>
                  <td className="col-7">
                    <div className="d-flex justify-content-between align-items-center h-50 b-bottom">
                      <span className="col-7 f-14">移動平均</span>
                      <span className="f-28">{PM2_AVG || '–'}</span>
                    </div>
                    <div className="d-flex justify-content-between align-items-center h-50">
                      <span className="col-7 f-14">小時濃度</span>
                      <span className="f-28">{PM2 || '–'}</span>
                    </div>
                  </td>
                </tr>
                <tr className="row mx-0">
                  <td className="col-5">
                    <div className="d-flex flex-column p-2">
                      <span>
                        PM<sub>10</sub>
                      </span>
                      <span className="f-14">
                        (μg/m<sup>3</sup>)
                      </span>
                      <span>懸浮微粒</span>
                    </div>
                  </td>
                  <td className="col-7">
                    <div className="d-flex justify-content-between align-items-center h-50 b-bottom">
                      <span className="col-7 f-14">移動平均</span>
                      <span className="f-28">{PM10_AVG || '–'}</span>
                    </div>
                    <div className="d-flex justify-content-between align-items-center h-50">
                      <span className="col-7 f-14">小時濃度</span>
                      <span className="f-28">{PM10 || '–'}</span>
                    </div>
                  </td>
                </tr>
                <tr className="row mx-0">
                  <td className="col-5">
                    <div className="d-flex flex-column p-2">
                      <span>
                        O<sub>3</sub>
                      </span>
                      <span className="f-14">(ppb)</span>
                      <span>臭氧</span>
                    </div>
                  </td>
                  <td className="col-7">
                    <div className="d-flex justify-content-between align-items-center h-50 b-bottom">
                      <span className="col-7 f-14">8小時移動平均</span>
                      <span className="f-28">{O3_8hr || '–'}</span>
                    </div>
                    <div className="d-flex justify-content-between align-items-center h-50">
                      <span className="col-7 f-14">小時濃度</span>
                      <span className="f-28">{O3 || '–'}</span>
                    </div>
                  </td>
                </tr>
                <tr className="row mx-0">
                  <td className="col-5">
                    <div className="d-flex flex-column p-2">
                      <span>CO</span>
                      <span className="f-14">(ppm)</span>
                      <span>一氧化碳</span>
                    </div>
                  </td>
                  <td className="col-7">
                    <div className="d-flex justify-content-between align-items-center h-50 b-bottom">
                      <span className="col-7 f-14">8小時移動平均</span>
                      <span className="f-28">{CO_8hr || '–'}</span>
                    </div>
                    <div className="d-flex justify-content-between align-items-center h-50">
                      <span className="col-7 f-14">小時濃度</span>
                      <span className="f-28">{CO || '–'}</span>
                    </div>
                  </td>
                </tr>
                <tr className="row mx-0">
                  <td className="col-5">
                    <div className="d-flex flex-column p-2">
                      <span>
                        SO<sub>2</sub>
                      </span>
                      <span className="f-14">(ppb)</span>
                      <span>二氧化硫</span>
                    </div>
                  </td>
                  <td className="col-7">
                    <div className="d-flex justify-content-between align-items-center h-50">
                      <span></span>
                      <span></span>
                    </div>
                    <div className="d-flex justify-content-between align-items-center h-50">
                      <span className="col-7 f-14">小時濃度</span>
                      <span className="f-28">{SO2 || '–'}</span>
                    </div>
                  </td>
                </tr>
                <tr className="row mx-0">
                  <td className="col-5">
                    <div className="d-flex flex-column p-2">
                      <span>
                        NO<sub>2</sub>
                      </span>
                      <span className="f-14">(ppb)</span>
                      <span>二氧化氮</span>
                    </div>
                  </td>
                  <td className="col-7">
                    <div className="d-flex justify-content-between align-items-center h-50">
                      <span></span>
                      <span></span>
                    </div>
                    <div className="d-flex justify-content-between align-items-center h-50">
                      <span className="col-7 f-14">小時濃度</span>
                      <span className="f-28">{NO2 || '–'}</span>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      {children}
      <div className="position-relative video">
        <img
          className="video_bg"
          src="./img/video-background.webp"
          alt=""
        ></img>
        <div className="video_mask"></div>
        <div className="row mx-0 py-5 video__container">
          <div className="col-12 col-md-6 px-3 px-sm-5 ps-md-5 py-4 d-flex flex-column justify-content-center align-items-center">
            <div className="d-flex align-items-center w-100 px-3 py-2 mb-3 video__title">
              <div className="d-flex align-items-center icon">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  height="30"
                  viewBox="0 0 24 24"
                  width="30"
                >
                  <path
                    clipRule="evenodd"
                    d="M2 8C2 6.34315 3.34315 5 5 5H14.5C16.1569 5 17.5 6.34315 17.5 8V8.46124L20.5939 7.08619C20.9032 6.94871 21.2611 6.97705 21.5449 7.16151C21.8287 7.34596 22 7.66149 22 8V16C22 16.3385 21.8287 16.654 21.5449 16.8385C21.2611 17.0229 20.9032 17.0513 20.5939 16.9138L17.5 15.5388V16C17.5 17.6569 16.1569 19 14.5 19H5C3.34315 19 2 17.6569 2 16V8ZM10 8H6V10H10V8Z"
                    fill="#fff"
                    fillRule="evenodd"
                  />
                </svg>
              </div>
              <span className="ms-2">空品影音</span>
            </div>
            <div className="video__inner">
              <iframe
                width="560"
                height="315"
                src="https://www.youtube-nocookie.com/embed/nwg2penu7Cs"
                title="YouTube video player"
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
              ></iframe>
            </div>
          </div>
          <div className="col-12 col-md-6 px-3 px-sm-5 pe-md-5 py-4 d-flex flex-column justify-content-center align-items-center">
            <div className="d-flex align-items-center w-100 px-3 py-2 mb-3 video__title">
              <div className="d-flex align-items-center icon">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  height="30"
                  viewBox="0 0 24 24"
                  width="30"
                >
                  <path
                    clipRule="evenodd"
                    d="M2 8C2 6.34315 3.34315 5 5 5H14.5C16.1569 5 17.5 6.34315 17.5 8V8.46124L20.5939 7.08619C20.9032 6.94871 21.2611 6.97705 21.5449 7.16151C21.8287 7.34596 22 7.66149 22 8V16C22 16.3385 21.8287 16.654 21.5449 16.8385C21.2611 17.0229 20.9032 17.0513 20.5939 16.9138L17.5 15.5388V16C17.5 17.6569 16.1569 19 14.5 19H5C3.34315 19 2 17.6569 2 16V8ZM10 8H6V10H10V8Z"
                    fill="#fff"
                    fillRule="evenodd"
                  />
                </svg>
              </div>
              <span className="ms-2">空品影音</span>
            </div>
            <div className="video__inner">
              <iframe
                width="560"
                height="315"
                src="https://www.youtube-nocookie.com/embed/vJqlr_JWgik"
                title="YouTube video player"
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
              ></iframe>
            </div>
          </div>
        </div>
      </div>
    </Wrapper>
  );
}

export default Main;
