import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.nav`
  position: relative;
  overflow: hidden;
  .select {
    position: relative;
    z-index: 2;
    .select__inner {
      select {
        width: 94px;
        padding: 5px 0 5px 5px;
        border: 1px solid #ccc;
        border-radius: 0.25rem;
        font-size: 14px;
        background-color: #ffffffb3;
      }
      .pin-btn {
        position: absolute;
        top: 0;
        right: -85px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 70px;
        height: 30px;
        border: 1px solid #eee;
        cursor: pointer;
      }
      .pin {
        position: absolute;
        top: 0;
        right: -30px;
        display: none;
        justify-content: center;
        align-items: center;
        width: 25px;
        height: 25px;
        cursor: pointer;
        svg {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  @media (max-width: 479.98px) {
    select {
      width: 80px !important;
    }
    .pin-btn {
      display: none !important;
    }
    .pin {
      display: flex !important;
    }
  }
  @media (max-width: 374.98px) {
    label {
      display: flex;
      flex-direction: column;
    }
    .pin {
      right: -24px !important;
    }
  }
`;

const Nav = ({
  selectedCounty,
  selectedSite,
  options,
  changeCounty,
  changeSite,
  color,
  handleClick,
}) => {
  return (
    <Wrapper>
      <div className="container-xl position-relative">
        <div className="d-flex justify-content-center p-3 select">
          <div className="position-relative d-flex justify-content-center select__inner">
            <label className="me-2">
              <span>縣市 : </span>
              <select
                value={selectedCounty}
                id="county"
                onChange={(e) => {
                  changeCounty(e);
                }}
              >
                <option value="Keelung">基隆市</option>
                <option value="Taipei">臺北市</option>
                <option value="Newtaipei">新北市</option>
                <option value="Taoyuan">桃園市</option>
                <option value="Hsinchu_city">新竹市</option>
                <option value="Hsinchu">新竹縣</option>
                <option value="Miaoli">苗栗縣</option>
                <option value="Taichung">臺中市</option>
                <option value="Nantou">南投縣</option>
                <option value="Changhua">彰化縣</option>
                <option value="Yunlin">雲林縣</option>
                <option value="Chiayi_city">嘉義市</option>
                <option value="Chiayi">嘉義縣</option>
                <option value="Tainan">臺南市</option>
                <option value="Kaohsiung">高雄市</option>
                <option value="Pingtung">屏東縣</option>
                <option value="Yilan">宜蘭縣</option>
                <option value="Hualien">花蓮縣</option>
                <option value="Taitung">臺東縣</option>
                <option value="Penghu">澎湖縣</option>
                <option value="Kinmen">金門縣</option>
                <option value="Lienchiang">連江縣</option>
              </select>
            </label>
            <label>
              <span>測站 : </span>
              <select
                value={selectedSite}
                id="site"
                onChange={(e) => {
                  changeSite(e);
                }}
              >
                {options}
              </select>
            </label>
            <button
              type="button"
              className="btn btn-light mx-2 ps-2 pe-1 pin-btn"
              title="釘選測站"
              onClick={handleClick}
            >
              <span>釘選</span>
              <svg
                fill="none"
                height="20"
                viewBox="0 0 20 20"
                width="20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M13.3251 2.61804C12.3599 1.65275 10.7326 1.91683 10.1221 3.13783L8.39285 6.59628C8.22855 6.92486 7.94939 7.18173 7.60829 7.31817L4.01834 8.75415C3.35177 9.02078 3.17498 9.88209 3.68262 10.3897L6.29289 13L3 16.2929V17H3.70711L7 13.7071L9.61027 16.3174C10.1179 16.825 10.9792 16.6482 11.2459 15.9817L12.6818 12.3917C12.8183 12.0506 13.0751 11.7715 13.4037 11.6072L16.8622 9.87793C18.0832 9.26743 18.3472 7.64015 17.382 6.67486L13.3251 2.61804Z"
                  fill={color}
                />
              </svg>
            </button>
            <div className="pin" title="釘選測站" onClick={handleClick}>
              <svg
                fill="none"
                height="20"
                viewBox="0 0 20 20"
                width="20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M13.3251 2.61804C12.3599 1.65275 10.7326 1.91683 10.1221 3.13783L8.39285 6.59628C8.22855 6.92486 7.94939 7.18173 7.60829 7.31817L4.01834 8.75415C3.35177 9.02078 3.17498 9.88209 3.68262 10.3897L6.29289 13L3 16.2929V17H3.70711L7 13.7071L9.61027 16.3174C10.1179 16.825 10.9792 16.6482 11.2459 15.9817L12.6818 12.3917C12.8183 12.0506 13.0751 11.7715 13.4037 11.6072L16.8622 9.87793C18.0832 9.26743 18.3472 7.64015 17.382 6.67486L13.3251 2.61804Z"
                  fill={color}
                />
              </svg>
            </div>
          </div>
        </div>
      </div>
    </Wrapper>
  );
};

export default Nav;
